import React, {Component} from "react";
import {PropTypes} from "prop-types";
import {connect} from "react-redux";
import {OraButton, OraMessage} from "eshop/components/OraCommonComponents";
import {
    getEntryForLowerInstallmentsModal,
    getLowerInstallmentsBundleNo,
    isLowerInstallmentsModalVisible
} from "eshop/modules/cart/selectors";
import {lowerInstallmentsModaClose} from "eshop/modules/cart/actions/resources";

export default class SliderComponent extends Component {

    constructor(props) {
        super(props);
    }

    state = {
        showMinError: false,
        showMaxError: false,
        maxPrice: false,
        minPrice: false,
        count: false,
        countIn: false,
        fromInput: true,
        diff: 0
    };

    componentWillReceiveProps(nextProps) {
        console.log("SliderComponent.componentWillReceiveProps.props", this.props);
        console.log("SliderComponent.componentWillReceiveProps.nextProps", nextProps);
        const device = this.getDevice(nextProps.entry);
        let maxPrice = this.getMaxPrice(device, nextProps.showNetPrices);
        let willBeVoucher = this.isVoucher(device);
        const currentDevice = this.getDevice(this.props.entry);
        let currentDiff = this.getOffset(currentDevice, this.getTaxFactor(currentDevice), nextProps);
        const taxFactor = this.getTaxFactor(device);
        let nextDiff = this.getOffset(device, taxFactor, nextProps);
        let minPrice = this.getMinPrice(device, nextProps.showNetPrices, nextDiff);
        console.log("WILL BE VOUCHER")
        console.log(willBeVoucher)
        let isVoucher = this.isVoucher(this.getDevice(this.props.entry));
        console.log("IS VOUCHER")
        console.log(isVoucher)
        if (this.state.count == false || maxPrice !== this.state.maxPrice || minPrice!==this.state.minPrice || isVoucher !== willBeVoucher || currentDiff!=nextDiff || this.props.open != nextProps.open || this.props.showNetPrices !== nextProps.showNetPrices ||
            taxFactor !== this.state.taxFactor) {
            let count = Number(parseFloat(minPrice) + nextDiff).toFixed(2);
            this.setState({
                maxPrice: maxPrice,
                minPrice: minPrice,
                count:  count,
                countIn: this.getInstallment(count, nextProps),
                fromInput: true,
                diff : nextDiff,
                taxFactor: taxFactor
            });
        }
    }

    getTaxFactor = device => device.tax ? device.tax/100 + 1 : 1;

    getDevice = entry => !!entry && !!entry.terminals &&
        entry.terminals.length > 0 &&
        entry.terminals[0] || !!entry.euroSets &&
        entry.euroSets.length > 0 && entry.euroSets[0];

    onChange = e => {
        console.log("ONCHANGE1")
        let value = e.target.value.toString().replace(",", ".")
        const regex = /^\d*(\.\d{0,2}|\d?)$/;
        console.log("REGEXP_TEST")
        console.log(value)
        console.log(regex.test(value))
        if (regex.test(value)) {
            this.setState({
                showMinError: false,
                showMaxError: false,
                fromInput: true,
                count: value,
                countIn: this.getInstallment(parseFloat(value), this.props)
            })
        }
    }

    onMouseDown(event) {
        console.log("onMouseDown")
        this.setState({
            fromInput: false,
            count: event.target.value,
            countIn: this.getInstallment(event.currentTarget.value, this.props),
            showMinError: false,
            showMaxError: false
        })
    }

    onMouseUp(event) {
        console.log("onMouseUp")
        this.setState({
            fromInput: false,
            count: event.target.value,
            countIn: this.getInstallment(event.target.value, this.props),
            showMinError: false,
            showMaxError: false,
        })
    }

    onSliderChange(event) {
        console.log("onSliderChange")
        this.setState({
            fromInput: false,
            count: event.target.value,
            // countIn: this.getInstallment(event.target.value, this.props),
            showMinError: false,
            showMaxError: false
        })
    }

    onButtonClick() {
        console.log("BUTTON CLICKED");
        console.log(this.state.count);
        console.log(this.state.minPrice);
        console.log(this.state.maxPrice);
        if (isNaN(parseFloat(this.state.count)) || parseFloat(this.state.count) < this.state.minPrice) {
            console.log("MIN ERROR")
            this.setState({
                showMinError: true
            })
        } else if (parseFloat(this.state.count) > this.state.maxPrice) {
            console.log("MAX ERROR")
            this.setState({
                showMaxError: true
            })
        } else {
            console.log("SUBMIT INSTALLMENTS")
            console.log(Number(this.state.count - this.state.minPrice).toFixed(2));
            console.log(this.props.showNetPrices ? "B2B" : "B2C");
            this.setState({
                diff: this.state.count - this.state.minPrice
            })
            let offset = 0;
            if (this.props.cartIsNet && !this.props.showNetPrices) {
                offset = ((this.state.count - this.state.minPrice)/this.state.taxFactor).toFixed(2);
            } else {
                offset = (this.state.count - this.state.minPrice).toFixed(2);
            }
            this.props.postLowerInstallments(this.props.bundleNo, offset, this.props.showNetPrices ? "B2B" : "B2C");
        }
    }

    render() {
        let monthlyPricesTable = this.renderMonthlyPrices();
        let error = this.renderError();
        return (
            <div className="">
                <h3 className="opl-modal-title-space u-margin-l">Obniż ratę</h3>
                <p className="u-margin-l">Możesz obniżyć wysokość raty, płacąc więcej na start:</p>
                <div className="l-row u-margin-l">
                    <div className="l-col l-col-12 ">
                        <div className="slide-container">
                            <input className="opl-range" type="range" step="0.01" min={this.state.minPrice}
                                   value={this.state.fromInput ? parseFloat(this.state.count) : undefined}
                                   defaultValue={this.state.minPrice} max={this.state.maxPrice} id="myRange"
                                   onChange={this.onSliderChange.bind(this)}
                                   onMouseDown={this.state.fromInput ? this.onMouseDown.bind(this) : undefined}
                                   onMouseUp={this.onMouseUp.bind(this)}
                                   onTouchEnd={this.onMouseUp.bind(this)}
                                   onTouchStart={this.onMouseUp.bind(this)}
                            />
                            <div className="u-display__flex-wrap u-justify-content_space-between u-padding-top-s">
                                <p className="g-gray5-c">{this.state.minPrice.toString().replace(".", ",")} zł</p>
                                <p className="g-gray5-c">{this.state.maxPrice.toString().replace(".", ",")} zł</p>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="l-row">
                    <div className="l-col l-col-small-12 l-col-medium-5 l-col-5 ">
                        <div className="opl-floating-label-line u-margin-m o-floating-label"
                             id="mod-core/modules/floating-label-2">
                            <input type="text" id="input01" name="input01"
                                   value={this.state.count.toString().replace(".", ",")}
                                   className="is-not-empty"
                                   onChange={this.onChange.bind(this)}
                            />
                            <label className="label o-floating-label__placeholder o-floating-label__p-2"
                                   htmlFor="input01">Opłata na start</label><span
                            className="opl-floating-label-line--border"></span>
                        </div>
                        {error}
                    </div>
                    <div className="l-col l-col-small-12 l-col-medium-5 l-col-4  u-small-margin-m">
                        <table>
                            <caption className="u-acc-hide">Miesięczna rata</caption>
                            <thead>
                            <tr className="u-acc-hide">
                                <th className="um-table-60"></th>
                                <th className="um-table-20">Miesięczna rata</th>
                            </tr>
                            </thead>
                            <tbody>
                            <tr>
                                <td className="p um-table-60 u-padding-top-s u-padding-s g-gray6-c u-font-small">Miesięczna
                                    rata
                                </td>
                            </tr>
                            {!!monthlyPricesTable && monthlyPricesTable}
                            </tbody>
                        </table>
                    </div>
                </div>
                <div className="l-row">
                    <div
                        className="l-col l-col-small-12 l-col-medium-4 l-col-4  large-offset-by-8 medium-offset-by-8">
                        <button onClick={this.onButtonClick.bind(this)}
                                className="opl-btn opl-btn--primary opl-btn--medium o-btn u-w-100"><span
                            className="opl-ripple-box"><span className="opl-ripple-circle"></span></span>
                            {!!this.props.descriptions && this.props.descriptions.lowerInstallmentsSubmitButton || "Zatwierdź"}
                        </button>
                    </div>
                </div>
            </div>

        );
    }

    renderError() {
        if (!!this.state.showMinError || !!this.state.showMaxError) {
            let errorMsg = this.state.showMinError ?
                !!this.props.descriptions && this.props.descriptions.lowerInstallmentsMinError || "Opłata na start nie może być niższa niż w ofercie"
                :
                !!this.props.descriptions && this.props.descriptions.lowerInstallmentsMaxError || "Opłata na start nie może być wyższa niż w ofercie";
            return (
                <div className="opl-msg opl-msg--context opl-msg--error u-margin-top-s" aria-live="rude">
                    <div><p role="alert"
                            className="u-margin-left">
                        {errorMsg}
                    </p>
                    </div>
                </div>)
        } else {
            return
        }
    }

    renderMonthlyPrices() {
        if (!!this.state.countIn && this.state.countIn.length > 3) {
            if (this.state.countIn[2] != this.state.countIn[3]) {
                return [
                    this.step(1, this.state.countIn[1], this.state.countIn[0]),
                    this.step(this.state.countIn[1] + 1, null, this.state.countIn[2]),
                    this.step(this.state.countIn[4] + 1, this.state.countIn[5], this.state.countIn[3])
                ];
            } else {
                return [
                    this.step(1, this.state.countIn[1], this.state.countIn[0]),
                    this.step(this.state.countIn[4], this.state.countIn[5], this.state.countIn[3])
                ];
            }
        } else if (this.state.countIn[0] != this.state.countIn[1]) {
            return [this.step(1, null, this.state.countIn[0]), this.step(2, this.state.countIn[2], this.state.countIn[1])];
        } else {
            return this.step(1, this.state.countIn[2], this.state.countIn[0]);
        }
    }

    step(monthFrom, monthTo, value) {
        console.log("STEP")
        console.log(monthFrom);
        console.log(monthTo)
        console.log(value)
        return (!isNaN(value) && parseFloat(this.state.count) >= parseFloat(this.state.minPrice) && parseFloat(this.state.count) <= parseFloat(this.state.maxPrice) &&
            <tr>
                <td className="u-padding-s">{monthFrom}{!!monthTo && '-' + monthTo} mies.</td>
                <td className="u-font-bold u-small-padding u-padding-s u-text-right">{!!value && value.toString().replace(".", ",")} zł</td>
            </tr>);
    }

    getOffset(device, taxFactor, props) {
        if(!device){
            return false;
        }
        return (props.cartIsNet && !props.showNetPrices) ?
            device.priceOffset * taxFactor : device.priceOffset;
    }

    getMinPrice(device, showNetPrices, nextDiff) {//todo net Price
        if(!device){
            return false;
        }
        if (!!device && device.voucherNames == null) {
            console.log("BRAK VOUCHERA")
            if (!!showNetPrices) {
                return !!device && device.checkoutPrice.netPriceWithoutVouchers.toFixed(2);
            }
            return !!device && device.checkoutPrice.priceWithoutVouchers.toFixed(2);
        } else {
            let oneTimeVoucher = this.isOneTimeVoucher(device, showNetPrices, nextDiff);
            if (!oneTimeVoucher){
                console.log("VOUCHER RATY")
                if (!!showNetPrices) {
                    return !!device && device.checkoutPrice.netPriceWithoutVouchers.toFixed(2);
                }
                return !!device && device.checkoutPrice.priceWithoutVouchers.toFixed(2);
            } else if (oneTimeVoucher){
                console.log("VOUCHER 1 RATA")
                if (!!showNetPrices) {
                    return !!device && (device.checkoutPrice.netPrice - this.state.diff).toFixed(2);
                }
                return !!device && (device.checkoutPrice.price - this.state.diff).toFixed(2);
            } else {
                return !!device && device.checkoutPrice.price.toFixed(2);
            }
        }
    }

    isOneTimeVoucher(device, showNetPrices, nextDiff) {
        console.log(this.state.diff)
        console.log(nextDiff)
        console.log(device.checkoutPrice.netPriceWithoutVouchers)
        console.log(device.checkoutPrice.netPrice)
        if (nextDiff!==false && !!showNetPrices ? nextDiff+device.checkoutPrice.netPriceWithoutVouchers == device.checkoutPrice.netPrice : nextDiff+device.checkoutPrice.priceWithoutVouchers == device.checkoutPrice.price){
            console.log("THERE IS NO 1 TIME VOUCHER")
            return false;
        } else {
            console.log("IS 1 TIME VOUCHER")
            return true;
        }
    }

    isVoucher(device){
        console.log("CHECK IS VOUCHER")
        console.log(device)
        return !!device && device.voucherNames != null;
    }

    getMaxPrice(device, showNetPrices) {
        if (!!device && device.voucherNames == null) {
            if (!!showNetPrices) {
                return !!device && (Number(this.getMonthlyPrice(device.monthlyPrices, 'netPriceWithoutVouchers')) + device.checkoutPrice.netPriceWithoutVouchers).toFixed(2);
            }
            return !!device && (Number(this.getMonthlyPrice(device.monthlyPrices, 'priceWithoutVouchers')) + device.checkoutPrice.priceWithoutVouchers).toFixed(2);
        } else {
            console.log("VOUCHER MAX PRICE")
            if (!!showNetPrices) {
                return !!device && (Number(this.getMonthlyPrice(device.monthlyPrices, 'netPrice')) + device.checkoutPrice.netPrice).toFixed(2);
            }
            return !!device && (Number(this.getMonthlyPrice(device.monthlyPrices, 'price')) + device.checkoutPrice.price).toFixed(2);
        }
    }

    getMonthlyPrice(monthly, type) {
        let sum = 0;
        monthly.forEach(e => {
                sum = sum + (e.monthTo - e.monthFrom + 1) * e[type]
            }
        );
        return sum;
    }

    getInstallment(value, {entry, showNetPrices}) {
        let device = !!entry && !!entry.terminals && entry.terminals.length > 0 && entry.terminals[0] || !!entry.euroSets && entry.euroSets.length > 0 && entry.euroSets[0];
        console.log("INSTALLMENTS")
        if (!!device) {
            console.log("INSTALLMENTS1")
            console.log(value)
            console.log(entry)
            let compensation, inst, compensationInst;
            let maxPrice = this.getMaxPrice(device, showNetPrices);
            let diff = Number(maxPrice - value).toFixed(2);
            if (!!device.monthlyPrices && !!device.monthlyPrices[1] && device.monthlyPrices[0].price == 0) {//todo fix for more than one  installment
                console.log("INSTALLMENTS2A")
                let zeroMonthTo = device.monthlyPrices[0].monthTo;
                let firstMonthTo = device.monthlyPrices[1].monthTo;
                let firstMonthFrom = device.monthlyPrices[1].monthFrom;
                let months = device.monthlyPrices[device.monthlyPrices.length - 1].monthTo-device.monthlyPrices[0].monthTo;
                compensation = this.calculateCompensation(diff, months)
                inst = Math.floor(100 * diff / months) / 100;
                compensationInst = Number(inst + parseFloat(compensation)).toFixed(2);
                return [Number(0).toFixed(2), zeroMonthTo, compensationInst, Number(inst).toFixed(2), firstMonthFrom, firstMonthTo]
            } else {
                console.log("INSTALLMENTS2B")
                let months = device.monthlyPrices[device.monthlyPrices.length - 1].monthTo;
                compensation = this.calculateCompensation(diff, months)
                inst = Math.floor(100 * diff / months) / 100;
                compensationInst = Number(inst + parseFloat(compensation)).toFixed(2);
                return [compensationInst, Number(inst).toFixed(2), months];
            }
        }
        return false;
    }

    calculateCompensation(value, months) {
        let modulo = Number(100 * value).toFixed(0) % months;
        return Number(modulo / 100).toFixed(2);
    }
}